.form {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 300px;
}
.label {
  font-family: Roboto;
  font-size: 15px;
  line-height: 13px;
  letter-spacing: 0.03em;
  color: #757575;
  margin-top: 10px;
}
.input {
  margin-left: 10px;
  padding: 5px;
  border: none;
  border-radius: 5px;
  background-color: rgba(122, 43, 226, 0.137);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}
.button {
  display: block;
  font-family: Roboto;
  font-size: 15px;
  line-height: 13px;
  letter-spacing: 0.03em;
  margin-top: 10px;
  width: 100px;
  height: auto;
  padding: 5px;
  color: #525151;
  border-radius: 5px;
  border: none;
  background-color: rgba(122, 43, 226, 0.178);
}
.button:hover,
.button:focus {
  background-color: rgba(122, 43, 226, 0.281);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}
